import React, {useCallback, useState} from "react";
import PropTypes from "prop-types";
import {Avatar, ListItem, ListItemAvatar, ListItemText} from "@material-ui/core";
import ErrorIcon from "@material-ui/icons/Error";
import formatDistance from "date-fns/formatDistance";

function MessageListItem({message, divider}) {
  const [hasErrorOccurred, setHasErrorOccurred] = useState(false);
  const handleError = useCallback(() => setHasErrorOccurred(true), [setHasErrorOccurred]);

  return <ListItem divider={divider}>
    <ListItemAvatar>
      {hasErrorOccurred ? <ErrorIcon color="secondary"/> : <Avatar
        src={hasErrorOccurred ? null : message.profilePicUrl}
        onError={handleError}
      />}
    </ListItemAvatar>
    <ListItemText
      primary={message.text}
      secondary={ago(message.date)}/>
  </ListItem>;
}

function ago(date) {
  return `${formatDistance(date * 1000, new Date())} ago`;
}

MessageListItem.propTypes = {
  message: PropTypes.object,
  divider: PropTypes.bool
};

export default MessageListItem;
